$fortuneContainerSize: 280px;
$fortuneContainerSizeMobile: 264.73px;

$lightsContainerSize: 267px;
$lightsContainerSizeMobile: 266px;

$prizeCardHeight: 93.2px;
$prizeCardWidth: 85.47px;

@mixin mediaResize($screen, $size, $mobileSize, $selectors...) {
  @for $i from 0 to length($selectors) {
    #{nth($selectors, $i + 1)}: $size;
  }
  @media (max-width: $screen) {
    @for $i from 0 to length($selectors) {
      #{nth($selectors, $i + 1)}: $mobileSize;
    }
  }
}
