@import "sass/index";
@import "./variables.scss";
@import "../../variables.module.scss";

$desktop-gap: 10px;
$mobile-gap: 5px;
.root {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 296px;
  background: $default;
  @include backgroundOptions;
}

.lightContainer {
  @include mediaResize(
    $mobileScreen,
    $lightsContainerSize,
    $lightsContainerSizeMobile,
    "width",
    "height"
  );
  position: absolute;
}

.wheelContainer {
  position: relative;

  margin: auto;
  border-radius: 50%;
  @include mediaResize(
    $mobileScreen,
    $fortuneContainerSize,
    $fortuneContainerSizeMobile,
    "width",
    "height"
  );
  visibility: hidden;
}
.visible {
  visibility: visible;
}

.wheel {
  position: relative;
  border-radius: 50%;
  background-color: transparent;

  display: flex;
  justify-content: center;
  align-items: center;
}
.img {
  width: fit-content;
  height: auto;
}

.lightWrapper {
  position: absolute;
  box-sizing: border-box;
  top: 50%;
  left: 50%;
  width: 50%;
  transform-origin: center left;
  transform: translateY(-50%)
    rotate(
      calc(var(--fortune-light-nb) * (360deg / var(--fortune-nb-light, 1)))
    );
  color: white;
  text-align: right;
}

.arrow,
.fortuneEllipse {
  position: absolute;
  top: 50%;
  left: 50%;
}
.arrow {
  transform: translate(-40%, -39%);
  @media (max-width: 600px) {
    transform: translate(-40%, -21%);
  }
}
.fortuneEllipse {
  transform: translate(-45%, -42%);
  @media (max-width: 600px) {
    transform: translate(-45%, -30%);
  }
}

@keyframes light {
  from {
    transform: scale(0.1);
  }
  to {
    transform: scale(2);
  }
}
