@import "sass/index";
@import "./variables.scss";

.lightContainer {
  @include mediaResize(
    $mobileScreen,
    $lightsContainerSize,
    $lightsContainerSizeMobile,
    "width",
    "height"
  );
  position: relative;
}

.wheelContainer {
  position: relative;

  margin: auto;
  border-radius: 50%;
  @include mediaResize(
    $mobileScreen,
    $fortuneContainerSize,
    $fortuneContainerSizeMobile,
    "width",
    "height"
  );
}

.wheel {
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  background-color: transparent;

  display: flex;
  justify-content: center;
  align-items: center;
  @include mediaResize(
    $mobileScreen,
    $fortuneContainerSize,
    $fortuneContainerSizeMobile,
    "width",
    "height"
  );
}
.img {
  width: 100%;
  height: 100%;
  position: absolute;
}

.lightWrapper {
  position: absolute;
  box-sizing: border-box;
  top: 50%;
  left: 50%;
  width: 50%;
  transform-origin: center left;
  transform: translateY(-50%)
    rotate(
      calc(var(--fortune-light-nb) * (360deg / var(--fortune-nb-light, 1)))
    );
  color: white;
  text-align: right;
}

.light {
  position: absolute;
  top: -45px;
  right: 0;
  width: 26px;
  height: 26px;
  z-index: 100;
  animation: light 1s 0s infinite alternate;
}
.lightD {
  animation: light 1s 0s infinite alternate-reverse;
}
.arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-40%, -50%);
}
.fortuneEllipse {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-45%, -50%);
}

@keyframes light {
  from {
    transform: scale(0.1);
  }
  to {
    transform: scale(2);
  }
}
